@use '@/assets/style/function' as function;
@use '@/assets/style/mixin' as mixin;

.header {
  display: flex;
  justify-content: center;
  position: relative;
  height: 223px;
  border-radius: 25px;
  margin: 25px 0;
  background-color: function.color(black);
  &__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    color: function.color(white);
    font-weight: 500;
  }
  &__background {
    border-radius: 25px;
    opacity: 0.7;
    width: 100%;
    object-fit: cover;
  }
  @include mixin.break-to(small) {
    margin: 15px 0;
    &__home {
      height: 111px;
    }
    &__title {
      width: 70%;
    }
  }
}
