@use '@/assets/style/mixin' as mixin;

.hosting {
  &__header {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
    h1 {
      margin: 0;
      font-size: 36px;
      font-weight: 500;
    }
  }
  .collapse__wrapper {
    @include mixin.grid-card-responsive(2);
  }
  .tag__wrapper {
    display: flex;
    gap: 15px;
    margin: 20px 0;
  }
  .host__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    & .host {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 18px;
      font-weight: 500;
      text-align: end;
      &__picture {
        border-radius: 50%;
        height: 64px;
        width: 64px;
        margin-left: 15px;
        object-fit: contain;
      }
    }
  }
  @include mixin.break-to(small) {
    font-size: 14px;
    &__header {
      flex-direction: column;
      & h1 {
        font-size: 18px;
      }
    }
    .host__wrapper {
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
      & .host {
        font-size: 12px;
        &__picture {
          height: 32px;
          width: 32px;
        }
      }
    }
  }
}
